export default class Sheep {
  // Side = 'left' or 'right'
  /**
   *
   * @param {string} side
   */
  constructor(side) {
    if (side === "left") {
      this.position = {
        x: 100,
        y: 100
      };
    } else if (side === "right") {
      this.position = {
        x: 200,
        y: 100
      };
    } else {
      throw new Error("Sheep only accepts left or right.");
    }
  }

  /**
   *
   * @param {CanvasRenderingContext2D} ctx
   * @param {*} image
   */
  draw(ctx, image) {
    ctx.drawImage(image, this.position.x, this.position.y);
  }
}
