import Sheep from "/src/sheep";

let canvas = document.getElementById("main_display");
let sheep1_img = document.getElementById("spit");
let ctx = canvas.getContext("2d");

ctx.clearRect(0, 0, 100, 100);
ctx.fillRect(20, 20, 10, 10);

let sheep1 = new Sheep("left");
sheep1.draw(ctx, sheep1_img);
